export default function isValid(evt: any): string {
  let value: any;
  let target: any;
  if (evt instanceof Element) {
    value = evt.value;
    target = evt;
  } else {
    value = evt.target.value;
    target = evt.target;
  }
  let message = '';
  if (target.id === 'first_name' || target.id === 'second_name') {
    if (!value.match(/^[A-Z-]/g)) {
      message = ' ';
    } else if (value.length < 3) {
      message = ' 3 ';
    } else if (value.match(/\d+/g)) {
      message = ' ';
    } else if (value.match(/\s/g)) {
      message = ' ';
    } else if (!value.match(/^[a-zA-Z--0-9-]{0,}$/g)) {
      message = '   ';
    }
  }
  if (target.id === 'login' || target.id === 'auth_login') {
    if (value.length === 0) {
      message = '   ';
    } else if (value.length < 3) {
      message = ' 3 ';
    } else if (!value.match(/^[a-zA-Z0-9-_]/g)) { //   
      message = ' ';
    } else if (value.length > 20) {
      message = '  20 ';
    } else if (!value.match(/[a-zA-Z-]+/g)) {
      message = ' 1 ';
    } else if (value.match(/\s/g)) {
      message = ' ';
    } else if (!value.match(/^[a-zA-Z0-9-_]{3,20}$/g)) {
      message = '   ';
    }
  }
  if (target.id === 'email') { //      @  
    if (value.length < 3) {
      message = ' 3 ';
    } else if (value.match(/\s/g)) {
      message = ' ';
    } else if (!value.match(/^[a-zA-Z0-9-_]/g)) { //   
      message = ' ';
    } else if (!value.match(/^[a-zA-Z0-9-_@.]{0,}$/g)) {
      message = '   ';
    } else if (!value.match(/[@]/g)) {
      message = '  @';
    } else if (!value.match(/[.]/g)) {
      message = '   ';
    } else if (value.match(/[.]/g) && !value.match(/\w+[.]\w+/g)) {
      message = '  ';
    }
  }

  if (target.id === 'password' || target.id === 'auth_pass' || target.id === 'password_new') {
    if (!value.match(/^[a-zA-Z0-9-_]/g)) { //   
      message = ' ';
    } else if (value.length > 40) {
      message = '  40 ';
    } else if (!value.match(/\d+/g)) {
      message = '   ';
    } else if (!value.match(/[A-Z-]+/g)) {
      message = '   ';
    } else if (value.length < 8) {
      message = ' 8 ';
    }
  }

  if (target.id === 'phone') {
    if (!value.match(/^[0-9+]/g)) {
      message = '    ';
    } else if (!value.match(/\d+/g)) {
      message = '   ';
    } else if (value.length < 10) {
      message = '     10 ';
    } else if (value.match(/\s/g)) {
      message = '     ';
    } else if (value.match(/[A-Za-z--]+/g)) {
      message = '     ';
    } else if (value.length > 15) {
      message = '     15 ';
    }
  }
  return message;
}
